import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class RxDefinitionNameService {
  getDisplayName(definitionName: string): string {
    const name = definitionName || '';

    return name.substring(name.lastIndexOf(':') + 1);
  }

  getDisplayNameForValidation(definitionName: string): string {
    const name = definitionName || '';

    return name.substring(name.indexOf(':') + 1);
  }

  getDefinitionName(bundleId: string, displayName: string): string {
    return `${bundleId}:${displayName}`;
  }

  getBundleId(definitionName: string): string {
    const name = definitionName || '';
    const matches = name.match(/(.+):/);

    return (matches && matches.pop()) || '';
  }
}
